.wrapper {
  overflow: hidden;
}
.btn-white__outline {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  &:hover {
    background: #A51EFF;
    border-color: #A51EFF;
  }
  span {
    font-size: 18px;
    line-height: 140%;
  }
}
.btn-blue {
  background: linear-gradient(10.03deg, rgb(67, 85, 255) 34.195%,rgb(125, 138, 255) 90.948%);
  background-color: #4355FF;
  color: #fff;
  &:hover {
    background: #4355FF;
  }
  span {
    line-height: 120%;
  }
}
.h2 {
  font-size: 48px;
  line-height: 120%;
}
.violet {
  color: #A51EFF;
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 32px;
  }
}
