.main {
  position: relative;
  z-index: 11;
  padding: 60px 0 90px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #0E0E0E;
    border: 0.5px solid #fff;
    border-radius: 10px;
  }
  &__desc {
    width: 60%;
    padding: 45px 0 25px 50px;
  }
  &__img {
    width: 40%;
    display: flex;
    align-items: flex-end;
    img {
      width: calc(100% + 80px);
      margin-left: -80px;
    }
  }
  &__title {
    font-size: 68px;
    line-height: 120%;
  }
  &__text {
    max-width: 610px;
    font-size: 20px;
    line-height: 120%;
    margin-top: 18px;
  }
  &__btn {
    height: 65px;
    gap: 5px;
    border-radius: 16px;
    padding: 5px 42px;
    margin-top: 18px;
    i {
      width: 11px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
    }
  }
  &__items {
    position: relative;
    max-width: 450px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3));
    background: linear-gradient(0.00deg, rgba(23, 34, 53, 0.12) 8.73%,rgba(26, 38, 58, 0.37) 100%);
    border: 0.5px solid rgba(224, 224, 224, 0.5);
    border-radius: 8px;
    margin-top: 32px;
  }
  &__item {
    position: relative;
    z-index: 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 15px 10px 10px;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: calc(100% - 24px);
        background: linear-gradient(
                        to bottom,
                        #0D0D0D 0%,
                        #E0E0E0 50%,
                        #0D0D0D 100%
        );
        pointer-events: none;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 100%;
      margin-bottom: 5px;
    }
    &-number {
      font-size: 24px;
      line-height: 100%;
      background: linear-gradient(90.00deg, rgb(255, 255, 255),rgb(201, 198, 198),rgb(255, 255, 255),rgb(185, 185, 185),rgb(255, 255, 255));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      margin-top: auto;
      margin-bottom: 5px;
    }
    &-icon {
      width: 10px;
      img {
        width: 100%;
      }
    }
  }
  .founder {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-top: 12px;
    &-img {
      width: 60px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-name {
      font-family: "RFDewiExpanded";
      font-size: 12px;
      line-height: 120%;
      font-weight: 700;
    }
    &-text {
      font-family: "RFDewiExpanded";
      font-size: 12px;
      line-height: 120%;
      margin-top: 5px;
    }
  }
}
.z-index {
  z-index: 1;
}
.info {
  padding: 90px 0 20px;
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 30px 20px;
    margin-top: 90px;
  }
  &__item {
    position: relative;
    min-height: 400px;
    background-image: url("../img/info-img2.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #1D1D1D;
    border: 1px solid #4B4B4B;
    border-radius: 10px;
    padding: 32px 26px;
    &-icon {
      position: absolute;
      top: -45px;
      right: 5px;
      max-width: 95px;
      pointer-events: none;
      img {
        max-width: 100%;
      }
    }
    &-title {
      font-family: "RFDewiExpanded";
      font-size: 28px;
      line-height: 120%;
    }
    &-text {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      margin-top: 10px;
      p {
        font-size: 20px;
        line-height: 140%;
      }
    }
  }
}
.banner {
  padding: 20px 0 85px;
  &__inner {
    position: relative;
    background-image: url("../img/banner-img3.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: #0E0E0E;
    border: 1px solid #fff;
    border-radius: 10px;
    padding: 64px 250px;
    overflow: hidden;
  }
  &__wrap {
    position: relative;
    z-index: 2;
    width: 100%;
    height: 100%;
  }
  &__title {
    text-align: center;
    font-family: "RFDewiExpanded";
    font-size: 32px;
    line-height: 120%;
    margin-bottom: 15px;
  }
  &__text {
    text-align: center;
    font-size: 24px;
    line-height: 120%;
  }
  &__img {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: 0;
    width: 230px;
    img {
      width: 100%;
    }
  }
  .circle {
    position: absolute;
    top: -50px;
    right: 50px;
    width: 370px;
    height: 170px;
    background: #1E34FF;
    filter: blur(140px);
    pointer-events: none;
  }
}
.info2 {
  padding: 85px 0 210px;
  &__wrap {
    position: relative;
    width: 100%;
    margin: 7.188em auto 0;
    font-size: calc(1420px * (16 / 1420));
    @media screen and (max-width: 1420px) {
      font-size: calc(100vw * (16 / 1420));
    }
    @media screen and (max-width: 992px) {
      font-size: calc(100vw * (16 / 768));
    }
    @media screen and (max-width: 576px) {
      font-size: calc(100vw * (16 / 360));
    }
  }
  &__img {
    position: relative;
    z-index: 2;
    max-width: 25.938em;
    width: 100%;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  .circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 22.125em;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .circle2 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 30em;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  .circle3 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 39.5em;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__item {
    position: absolute;
    max-width: 40em;
    &:nth-child(1) {
      top: 0;
      left: -5.8em;
      .info2__item-arrow {
        top: calc(100% + 0.5em);
        right: 1.25em;
      }
    }
    &:nth-child(2) {
      top: 8.75em;
      left: -5.5em;
      .info2__item-arrow {
        top: calc(100% + 0.5em);
        right: -1.875em;
      }
    }
    &:nth-child(3) {
      left: -6.5em;
      bottom: 6.25em;
      .info2__item-arrow {
        top: calc(100% + 0.5em);
        right: -1.125em;
      }
    }
    &:nth-child(4) {
      top: 0;
      right: -5.625em;
      .info2__item-arrow {
        top: calc(100% - 1.5em);
        left: -1em;
      }
    }
    &:nth-child(5) {
      top: 9.25em;
      right: -10.625em;
      .info2__item-arrow {
        top: calc(100% - 1.5em);
        left: -1em;
      }
    }
    &:nth-child(6) {
      right: -6.5em;
      bottom: 6.25em;
      .info2__item-arrow {
        top: calc(100% + 0.5em);
        left: -1.5em;
      }
    }
    &-text {
      display: flex;
      align-items: start;
      font-family: "RFDewiExpanded";
      font-size: 1.5em;
      line-height: 120%;
      span {
        margin-right: 10px;
      }
    }
    &-arrow {
      position: absolute;
      max-width: 3.75em;
      img {
        max-width: 100%;
      }
    }
  }
}

@media screen and (max-width: 1800px) {
  .info2 {
    &__item {
      max-width: 28em;
      &:nth-child(1) {
        left: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
        left: 0;
        .info2__item-arrow {
          right: -1.5em;
        }
      }
      &:nth-child(3) {
        left: 0;
        bottom: 0;
      }
      &:nth-child(4) {
        right: 0;
      }
      &:nth-child(5) {
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
      }
      &:nth-child(6) {
        right: 0;
        bottom: 0;
        .info2__item-arrow {
          top: 100%;
          transform: rotate(40deg);
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 20px 0 40px;
    &__inner {
      flex-direction: column;
      gap: 30px;
      padding: 30px 30px 0 30px;
    }
    &__desc {
      width: 100%;
      padding: 0;
    }
    &__img {
      max-width: 645px;
      width: 100%;
      margin: 0 auto;
      img {
        width: 100%;
        margin: 0;
      }
    }
    &__title {
      font-size: 48px;
    }
    &__text {
      max-width: 100%;
      font-size: 16px;
    }
    &__items {
      max-width: 100%;
    }
  }
  .info {
    padding: 40px 0 20px;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
      margin-top: 60px;
    }
    &__item {
      &-title {
        font-size: 24px;
      }
      &-text {
        p {
          font-size: 16px;
        }
      }
    }
  }
  .banner {
    padding: 20px 0 40px;
    &__inner {
      padding: 64px 100px;
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      font-size: 16px;
    }
    &__img {
      top: -20px;
      left: -15px;
      bottom: unset;
      transform: rotate(40deg);
      width: 100px;
      height: auto;
      img {
        height: auto;
      }
    }
    &__img2 {
      width: 100px;
    }
    .circle {
      top: -10px;
      right: 10px;
      width: 200px;
      height: 80px;
      filter: blur(80px);
    }
  }
  .info2 {
    padding: 40px 0 160px;
    &__img {
      max-width: 16em;
    }
    .circle {
      width: 20em;
    }
    .circle2 {
      width: 25em;
    }
    .circle3 {
      width: 30em;
    }
    &__item {
      max-width: 13em;
      &-text {
        font-size: 0.75em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-top: 25px;
    &__inner {
      padding: 24px 16px 0 16px;
    }
    &__title {
      font-size: 36px;
    }
  }
  .info {
    &__items {
      grid-template-columns: 100%;
    }
    &__item {
      min-height: unset;
      padding: 32px 16px;
      &-title {
        font-size: 20px;
      }
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
  .banner {
    &__inner {
      padding: 40px;
    }
    &__img {
      width: 80px;
    }
    &__img2 {
      width: 80px;
    }
  }
  .info2 {
    padding-bottom: 80px;
    &__wrap {
      margin-top: 3.75em;
    }
    &__img {
      max-width: 7.5em;
    }
    .circle {
      width: 10em;
    }
    .circle2 {
      width: 12em;
    }
    .circle3 {
      width: 14em;
    }
    &__item {
      max-width: 6em;
      &:nth-child(2) {
        .info2__item-arrow {
          right: -0.5em;
        }
      }
      &:nth-child(3) {
        .info2__item-arrow {
          top: unset;
          bottom: 0;
        }
      }
      &:nth-child(5) {
        .info2__item-arrow {
          left: -0.5em;
        }
      }
      &:nth-child(6) {
        .info2__item-arrow {
          top: unset;
          bottom: 0;
        }
      }
      &-text {
        font-size: 0.375em;
      }
      &-arrow {
        max-width: 1em;
      }
    }
  }
}