.header {
  position: relative;
  z-index: 10;
  padding: 30px 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 25px;
    &:before {
      content: '';
      position: absolute;
      top: -130px;
      left: 30px;
      width: 265px;
      height: 265px;
      background: #5E08A0;
      filter: blur(120px);
      border-radius: 50%;
      pointer-events: none;
    }
  }
  &__logo {
    position: relative;
    z-index: 2;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 10px;
    &-icon {
      width: 68px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-family: "RFDewiExpanded";
      font-size: 12px;
      line-height: 100%;
      strong {
        font-weight: 700;
        color: #A51EFF;
      }
    }
  }
  &__group {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 25px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 25px;
    margin-right: 25px;
    li a {
      font-size: 18px;
      line-height: 166%;
      &:hover {
        color: #A51EFF;
      }
    }
  }
  &__info {
    max-width: 157px;
    display: flex;
    align-items: center;
    gap: 9px;
    background-image: url("../img/info-img.png");
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: cover;
    background-color: linear-gradient(90.00deg, rgb(134, 42, 197),rgb(200, 121, 255) 54%,rgb(135, 43, 200) 100%);
    box-shadow: 0px 2.2px 0px 0px rgb(106, 15, 168);
    border-radius: 6px;
    padding: 8px;
    &-icon {
      width: 32px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 100%;
    }
  }
  &__phones-text {
    font-size: 18px;
    line-height: 140%;
    margin-bottom: 5px;
  }
  &__phone {
    display: block;
    font-size: 18px;
    line-height: 140%;
    &:not(:last-child) {
      margin-bottom: 5px;
    }
    &:hover {
      color: #A51EFF;
    }
  }
  &__btn {
    height: 54px;
    border-radius: 22px;
    padding: 5px 25px;
    span {
      font-size: 18px;
    }
  }
  &__group2 {
    display: none;
  }
}
.burger {
  position: relative;
  z-index: 15;
  width: 30px;
  height: 20px;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background: #A51EFF;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 9.5px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 8px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1500px) {
  .header {
    &__list {
      margin-right: 0;
      li a {
        font-size: 16px;
      }
    }
    &__phones-text {
      font-size: 16px;
    }
    &__phone {
      font-size: 16px;
    }
    &__btn {
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__list {
      li a {
        font-size: 14px;
      }
    }
    &__phones-text {
      font-size: 14px;
    }
    &__phone {
      font-size: 14px;
    }
    &__btn {
      height: 44px;
      padding: 5px 20px;
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .header {
    padding: 20px 0;
    &__logo {
      &-icon {
        width: 40px;
      }
      &-text {
        font-size: 10px;
      }
    }
    &__list {
      li a {
        font-size: 13px;
      }
    }
    &__phones-text {
      font-size: 13px;
    }
    &__phone {
      font-size: 13px;
    }
    &__btn {
      span {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__inner {
      &:before {
        top: -80px;
        width: 200px;
        height: 200px;
        filter: blur(90px);
      }
    }
    &__group {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #1D1D1D;
      padding: 120px 15px 50px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &__list {
      flex-direction: column;
      li a {
        font-size: 16px;
      }
    }
    &__phones-text {
      font-size: 16px;
    }
    &__phone {
      font-size: 16px;
    }
    &__btn {
      height: 54px;
      padding: 5px 25px;
      span {
        font-size: 16px;
      }
    }
    &__group2 {
      display: flex;
      align-items: center;
      gap: 25px;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__group2 {
      .header__info {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 15px 0;
    &__group2 {
      .header__phones-text {
        font-size: 14px;
      }
      .header__phone {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .header {
    &__group2 {
      .header__phones-wrap {
        display: none;
      }
    }
  }
}
